	import Vue from 'vue'
	import moment from 'moment'
	import util from '@/libs/util'
	import {_} from '@/libs/util'
	import common from '@/libs/common.mixin'
	import globals from '@/libs/globals.mixin'
	import XTable from 'c%/table'
	import XPage from 'c%/page'
	
	export default{
		mixins: [ common,globals ],
		components: {
			XTable,
			XPage
		},
		props: {
			group: {
				type: String,
				default: 'bid'
			},
			bids: {
				type: Array,
				default: []
			},
			yearMonth: {
				type: String,
				default: moment(new Date()).format('YYYY-MM')
			}
		},
		data () {
			return {
				data: []				
			}
		},
		watch: {
			bids: function (val) {
				this.pageIndex = 1
				this.init_data()
			},
			group: function (val) {
				this.pageIndex = 1
				this.init_data()
			},
			yearMonth: function (val) {
				this.pageIndex = 1
				this.init_data()
			}
		},
		mounted () {
			this.init_data()
		},
		methods: {			
			init_data () {				
				this.showLoading()
				this.$rest(this.data_uri).get()
				.success(data=>{
					this.data = data
					this.loadFinished()
				})
			},
			loadFinished () {
				let data = this.data
				
				if(data&&data.length>0){
					data.push({
						column_total:true
					})
				}
				
				setTimeout(()=>{
					this.hideLoading()
				},300)
			},
			//获取列统计
			getTotal(field) {
				let data = this.data,
					result = 0
				if(data&&data.length>0){
					data.forEach(d=>{
						if(d[field]){				
							result += Number(d[field])							
						}
					})
					
					if(!_.isNumber(data[data.length-2][field])){
						result = parseFloat(result).toFixed(2)	
					}
				}
				return result
			}
		},
		computed: {
			tableHeight () {
				let winHeight = window.innerHeight||document.innerHeight
				return winHeight - (46 + this.headerHeight) 
			},
			accounts () {
				return this.$store.state.gvars.accounts||[]
			},
			tally_types () {
				return this.$store.state.gvars.tally_types||[]
			},
			tally_helps () {
				return this.$store.state.gvars.tally_helps||[]
			},
			columns () {
				let columns = []
				let first_column = {}
				if(this.group=='aa_id'){
					first_column = {
						title: '账户',
						key: 'aa_id',
                        render: (h,params)=>{
                        	if(params.row.column_total){
                        		return '合计'
                        	}
                        	let obj = this.accounts.find(a=>a.aa_id===params.row.aa_id),
								result = '未指定'
							if(obj){
								result = obj.name
							}
							return h('span',result)                        	
                        }
					}
				}else if(this.group=='cate'){
					first_column = {
						title: '大类',
						key: 'cate',
						render: (h,params)=>{ 
							if(params.row.column_total){
                        		return '合计'
                        	}
							let map_types = {1:'收入',2:'支出',3:'转账',4:'应收',5:'应付'}
							return h('span',map_types[params.row.cate]||'未指定')                        	
                        }
					}
				}else if(this.group=='tt_id'){
					first_column = {
						title: '小类',
						key: 'tt_id',
						render: (h,params)=>{   
							if(params.row.column_total){
                        		return '合计'
                        	}                     	
                        	let obj = this.tally_types.find(t=>t.tt_id===params.row.tt_id),							
								result = '未指定'
							if(obj){
								result = obj.name
							}
							return h('span',result)
                        }
					}
				}else if(this.group=='client_th_id'){
					first_column = {
						title: '往来单位',
						key: 'client_th_id',
						render: (h,params)=>{
							if(params.row.column_total){
                        		return '合计'
                        	}
							let obj = this.tally_helps.find(t=>t.th_id===params.row.client_th_id),							
								result = '未指定'
							if(obj){
								result = obj.name
							}
							return h('span',result)
                        }
					}
				}else if(this.group=='item_th_id'){
					first_column = {
						title: '项目',
						key: 'item_th_id',
						render: (h,params)=>{
							if(params.row.column_total){
                        		return '合计'
                        	}
							let obj = this.tally_helps.find(t=>t.th_id===params.row.item_th_id),							
								result = '未指定'
							if(obj){
								result = obj.name
							}
							return h('span',result)
						}
					}
				}else if(this.group=='employee_th_id'){
					first_column = {
						title: '人员',
						key: 'employee_th_id',
						render: (h,params)=>{
							if(params.row.column_total){
                        		return '合计'
                        	}
							let obj = this.tally_helps.find(t=>t.th_id===params.row.employee_th_id),							
								result = '未指定'
							if(obj){
								result = obj.name
							}
							return h('span',result)
						}
					}
				}
				let column_income = {
						title: '收入',
						key: 'income',
						render: (h,params)=>{
							if(params.row.column_total){
								return h('span',{'class':'text-danger'},'+'+this.getTotal('income')) 
							}
							return params.row.income
						}
					},					
					column_payout = {
						title: '支出',
						key: 'payout',
						render: (h,params)=>{
							if(params.row.column_total){					
								return h('span',{'class':'text-danger'},'-'+this.getTotal('payout')) 
							}
							return params.row.payout
						}
					}					
					
				columns.push(first_column,column_income,column_payout)
				
				return columns
			},
			data_uri () {
				let group = this.group,
					start_date = this.yearMonth+'-01',
					end_date = this.yearMonth+'-'+moment(this.yearMonth).daysInMonth(),
					base_uri = 'tallys/do_stats',
					bids = ''

					if(this.bids.length>0){
						bids = util.sprintf('&bid=[In,%s]',this.bids.join(',')) 
					}
				let	uri = util.sprintf('%s?group=%s&int_day=[Between,%s,%s]%s',base_uri,group,start_date,end_date,bids)
										
				return uri
			}
		}
	}



