filter: ['aa_id'],
filterList:[
{ name:'按账户统计', value: 'aa_id' },
{ name:'按大类统计', value: 'cate' },
{ name:'按小类统计', value: 'tt_id' },
{ name:'按往来单位统计', value: 'client_th_id' },
{ name:'按项目统计', value: 'item_th_id' },
{ name:'按人员统计', value: 'employee_th_id' },
]
}
}
}




